.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.display {
    width: auto;
    margin-top: 10px;
    height: 360px;
    position: absolute;
    background-color: #5a5e67;
    border-color: #5a5e67;
    border-radius: 10px;
    border-width: 20px;
    border-style: solid;
    grid-template-columns: repeat(1, minmax(1%, 1fr));
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
}

.display:hover {
    box-shadow: 0px 10px 20px 5px rgba(0, 0, 0, 0.5);
}

#display {
    position: relaive;
    width: 90%;
    background-color: black;
    color: white;
    top: 80px;
    height: auto;
    margin-left: 10px;
    margin-right: 10px;
    border-color: black;
    border-radius: 100px;
    border-width: 3px;
    border-style: solid;
}

.button {
    color: orange;
    background-color: black;
    height: 60px;
    width: 69px;
    position: relative;
    justify-content: center;


}

button:hover {
    color: black;
    background-color: #cec6cc;
}

.numbers-container {
    position: relative;
    font-size: 20px;
    min-height: 20px;
    width: auto;
    top: 45px;
    border-style: solid;
    border-color: #5a5e67;
    border-width: 10px;
    background: #0f3391;
    display: grid;
    grid-template-columns: 2fr 2fr 2fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-gap: 5px;
}

.buttons {
    color: white;
    background-color: gray;
    height: 30px;
    width: 25%;
    left: 5px;
    text-align: center;
    position: relative;
}

.operations-container {
    position: relative;
    display: flex;
    height: auto;
    width: 100%;
    right: 5px;
    bottom: 250px;
}

h2 {
    margin-top: 1px;
    color: white;
    text-align: center;
    font-size: 16px;
}
